body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  background-color: #f7f7f7; /* Light gray background for the whole page */
  color: #333;
  line-height: 1.6;
}

.webgpu-unavailable-banner {
  background-color: #fff3cd; /* Light yellow */
  color: #856404; /* Dark yellow text */
  padding: 10px 20px;
  border-bottom: 1px solid #ffeeba; /* Slightly darker yellow border */
  text-align: center;
  font-size: 0.9em;
}

.webgpu-unavailable-banner p {
  margin: 0;
}

/* New main container for the entire app */
.App-container {
  display: flex;
  flex-direction: column; /* Stack top-bar (optional) and main content */
  min-height: 100vh;
  background-color: #fff; /* White background for the app window area */
  max-width: 1200px; /* Max width for the app, centered */
  margin: 0 auto; /* Center the app container */
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

/* Wrapper for the main content area (excluding optional top bar and footer) */
.main-content-wrapper {
  flex-grow: 1;
  display: flex;
  flex-direction: column; /* If a top bar within this wrapper is added */
}

/* Area that holds the two main panels */
.content-area {
  display: flex;
  flex-grow: 1;
  border-top: 1px solid #e0e0e0; /* Separator if there was a top bar */
}

/* Left Panel: Transcription */
.left-panel {
  width: 50%; /* Adjust as needed, e.g., 40% or 60% */
  padding: 20px;
  border-right: 1px solid #e0e0e0; /* Separator between panels */
  display: flex;
  flex-direction: column;
  background-color: #fdfdfd; /* Slightly off-white */
}

.left-panel-header {
  display: flex;
  flex-direction: column; /* Stack title input and then the toolbar */
  align-items: stretch; /* Make input and toolbar take full width */
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.left-panel-header h2 {
  margin: 0;
  font-size: 1.4em;
  color: #222;
}

.meeting-title-input {
  font-size: 1.4em; /* Match h2 styling */
  color: #222;
  border: none;
  background-color: transparent;
  padding: 5px;
  /* margin-right: 10px; */ /* Removed as it's full width now */
  flex-grow: 1; /* Allow it to take available space */
  margin-bottom: 15px; /* Space between title and the new toolbar */
  font-family: inherit; /* Inherit font from body/parent */
  font-weight: bold; /* Match h2 styling */
  border-bottom: 2px solid transparent; /* Placeholder for focus style */
  transition: border-bottom-color 0.2s ease;
}

.meeting-title-input:focus {
  outline: none;
  border-bottom-color: #007bff; /* Highlight on focus */
}


.left-panel-actions button,
.right-panel-actions button {
  padding: 8px 12px;
  font-size: 0.9em;
  cursor: pointer;
  background-color: #e9e9e9;
  border: 1px solid #d0d0d0;
  border-radius: 6px;
  color: #333;
  transition: background-color 0.2s ease, border-color 0.2s ease;
  /* margin-left: 8px; */ /* Removed, using gap on parent containers now */
}

.left-panel-actions button:hover:not(:disabled),
.right-panel-actions button:hover:not(:disabled) {
  background-color: #dcdcdc;
  border-color: #bcbcbc;
}

.left-panel-actions button:disabled,
.right-panel-actions button:disabled {
  background-color: #f5f5f5;
  color: #aaa;
  cursor: not-allowed;
  border-color: #e0e0e0;
}

/* New styles for the toolbar that groups all controls below the meeting title */
.panel-controls-toolbar {
  display: flex;
  justify-content: space-between; /* Audio controls on left, other actions on right */
  align-items: center;
  gap: 15px; /* Space between the two groups of controls (header-audio-controls and left-panel-actions) */
}

.panel-controls-toolbar .header-audio-controls,
.panel-controls-toolbar .left-panel-actions {
  display: flex;
  align-items: center;
  gap: 8px; /* Consistent spacing between buttons within each group */
}

/* Styling for buttons within .header-audio-controls (Mic, Record/Stop) */
/* These aim to be visually consistent with .left-panel-actions button styling */
.panel-controls-toolbar .header-audio-controls button {
  padding: 8px 12px;
  font-size: 0.9em;
  cursor: pointer;
  border-radius: 6px;
  transition: background-color 0.2s ease, border-color 0.2s ease;
  border: 1px solid #d0d0d0; /* Default border from action buttons */
  background-color: #e9e9e9; /* Default background from action buttons */
  color: #333; /* Default text color from action buttons */
  line-height: normal; /* Ensure text (and icons) are vertically centered */
}

.panel-controls-toolbar .header-audio-controls button:hover:not(:disabled) {
  background-color: #dcdcdc; /* Hover from action buttons */
  border-color: #bcbcbc;
}

.panel-controls-toolbar .header-audio-controls button:disabled {
  background-color: #f5f5f5; /* Disabled from action buttons */
  color: #aaa;
  cursor: not-allowed;
  border-color: #e0e0e0;
}

/* Specific overrides for Record button */
.panel-controls-toolbar .header-audio-controls .record-button {
  background-color: #e74c3c; /* A clear, friendly red */
  border-color: #c0392b; /* Darker red for border */
  color: #fff; /* White text for better contrast */
}
.panel-controls-toolbar .header-audio-controls .record-button:hover:not(:disabled) {
  background-color: #c0392b; /* Darker red on hover */
  border-color: #a93226;
}

/* Specific overrides for Stop button */
.panel-controls-toolbar .header-audio-controls .stop-button {
  background-color: #5D6D7E; /* A muted, serious dark blue/grey */
  border-color: #34495E; /* Darker border */
  color: #fff; /* White text */
}
.panel-controls-toolbar .header-audio-controls .stop-button:hover:not(:disabled) {
  background-color: #34495E; /* Darker on hover */
  border-color: #2C3E50;
}
/* End of new toolbar styles */

.left-panel-actions .icon-button,
.audio-controls .icon-button { /* For settings, mic, stop etc. */
  background-color: transparent;
  border: none;
  padding: 8px;
  font-size: 1.2em; /* Adjust for icon size */
}
.left-panel-actions .icon-button:hover:not(:disabled),
.audio-controls .icon-button:hover:not(:disabled) {
  background-color: #f0f0f0;
}


.transcription-display-area {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  background-color: #fff;
  margin-bottom: 15px;
  font-size: 0.95em;
  min-height: 300px; /* Ensure it has some height */
}

.transcription-display-area .placeholder-text {
  color: #888;
  font-style: italic;
}

.transcription-display-area.placeholder-mode {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.transcription-display-area.placeholder-mode .placeholder-text {
  font-size: 1em;
}


.confirmed-text {
  /* Standard text color, already inherited */
}

.unconfirmed-text {
  color: #777; /* Lighter color for unconfirmed text */
  font-style: italic;
}

.audio-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-top: 1px solid #eee;
  background-color: #f9f9f9;
  border-radius: 0 0 6px 6px; /* Rounded bottom corners if left-panel has rounded top */
}

.audio-controls .icon-button { /* Mic, Stop */
  font-size: 1.5em; /* Larger icons for main controls */
  color: #555;
}
.audio-controls .icon-button:hover:not(:disabled) {
  color: #222;
}


.progress-bar-container {
  display: flex;
  align-items: center;
  flex-grow: 1;
  margin: 0 15px;
}

.progress-bar-container span {
  font-size: 0.85em;
  color: #555;
}

.progress-bar {
  flex-grow: 1;
  height: 8px;
  background-color: #ddd;
  border-radius: 4px;
  margin: 0 10px;
  position: relative;
  overflow: hidden; /* For inner progress */
}

