.audio-controls .play-button {
  background-color: transparent;
  border: none;
  font-size: 2.2em; /* Large play/pause button */
  color: #007bff;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.audio-controls .play-button:hover:not(:disabled) {
  color: #0056b3;
}
.audio-controls .play-button:disabled {
  color: #aaa;
  cursor: not-allowed;
}

.audio-controls .record-button,
.audio-controls .stop-button {
  font-size: 1.8em; /* Slightly smaller than the old play button to manage unicode char size */
  padding: 5px 10px; /* Add some padding */
  border-radius: 50%; /* Make it circular if desired, or keep as is */
  line-height: 1; /* Adjust line height for better vertical centering of unicode */
}

.audio-controls .record-button {
  color: red;
}
.audio-controls .record-button:hover:not(:disabled) {
  color: darkred;
}

.audio-controls .stop-button {
  color: #cc0000; /* Darker red for stop */
}
.audio-controls .stop-button:hover:not(:disabled) {
  color: #a30000;
}


/* Right Panel: AI Enhanced Summary */
.right-panel {
  width: 50%; /* Adjust as needed */
  padding: 20px;
  display: flex;
  flex-direction: column;
  background-color: #fdfdfd;
}

.right-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.right-panel-header h3 {
  margin: 0;
  font-size: 1.2em;
  color: #222;
}

.summary-display-area {
  flex-grow: 1;
  overflow-y: auto;
  font-size: 0.95em;
}
.summary-display-area .placeholder-text {
  color: #888;
  font-style: italic;
  padding: 10px;
  border: 1px dashed #ddd;
  border-radius: 4px;
  text-align: center;
}

.loading-status {
  font-size: 0.85em;
  color: #555; /* A neutral, slightly muted color */
  margin-top: 5px; /* Space above the message */
  margin-bottom: 10px; /* Space below the message */
  padding: 5px 10px;
  background-color: #f0f0f0; /* Light background to make it stand out a bit */
  border-radius: 4px;
  text-align: center; /* Center the text if it's on its own line */
}


.summary-section {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #e8e8e8;
  border-radius: 6px;
  background-color: #fff;
}

.summary-section h4 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.1em;
  color: #333;
  display: flex;
  justify-content: space-between; /* For delete button */
  align-items: center;
}

.summary-section .delete-button {
  font-size: 0.8em;
  color: #999;
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
}
.summary-section .delete-button:hover {
  color: #d9534f; /* Red on hover */
}

.summary-section ul {
  padding-left: 20px;
  margin-bottom: 0;
}
.summary-section li {
  margin-bottom: 8px;
}

/* Footer */
.App-footer-condensed {
  background-color: #333;
  color: #f0f0f0;
  padding: 10px 20px;
  font-size: 0.85em;
  text-align: center;
  border-top: 1px solid #444;
}

.App-footer-condensed p {
  margin: 0;
}

/* Optional Sidebar */
.sidebar {
  width: 60px; /* Adjust as needed */
  background-color: #ececec; /* Slightly darker than main bg */
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 1px solid #dcdcdc;
}

.sidebar-icon {
  font-size: 1.8em; /* Adjust for icon size */
  color: #555;
  margin-bottom: 25px;
  cursor: pointer;
  transition: color 0.2s ease;
}

.sidebar-icon:hover {
  color: #007bff;
}

.sidebar-arrow {
  margin-top: auto; /* Pushes to bottom */
  margin-bottom: 20px;
  background-color: #d0d0d0;
  color: #fff;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1em;
}
.sidebar-arrow:hover {
  background-color: #bcbcbc;
}


/* Optional Top Bar */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #f8f9fa; /* Light background for top bar */
  border-bottom: 1px solid #e0e0e0;
}

.brand-name {
  font-size: 1.3em;
  font-weight: bold;
  color: #333;
}

.top-bar-actions {
  display: flex;
  gap: 15px;
}

.top-bar-icon {
  font-size: 1.2em; /* Adjust for icon size */
  color: #555;
  cursor: pointer;
  transition: color 0.2s ease;
}

.top-bar-icon:hover {
  color: #007bff;
}


